.link {
    text-decoration: none;
    font-weight: bold;
}

.navMenu {
    width: 400px;
    display: flex;
    justify-content: space-between;
}

.navMenu a {
    font-size: 18px;
    color: #ffd800;
    transition-duration: 0.5s;
}

.navMenu a:hover {
    transform: scale(1.4);
}